/* Auth Form Container */
.auth-form-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  background: linear-gradient(135deg, #f0f4f8 0%, #e2e8f0 100%);
}

/* Auth Form */
.auth-form {
  background: white;
  padding: 3rem;
  border-radius: 1rem;
  box-shadow: 0 10px 25px rgba(0,0,0,0.1);
  width: 100%;
  max-width: 28rem;
}

.auth-form h1 {
  font-size: 2rem;
  font-weight: 700;
  color: var(--color-primary);
  margin-bottom: 1rem;
  text-align: center;
}

.auth-form > p {
  color: var(--color-text-light);
  text-align: center;
  margin-bottom: 3rem;
  font-size: 1rem;
}

.auth-form form {
  margin-bottom: 2rem;
}

.form-group {
  margin-bottom: 2rem;
}

.form-group label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--color-text-secondary);
  margin-bottom: 0.75rem;
}

.form-input {
  width: 100%;
  padding: 1rem;
  border: 2px solid var(--color-background-dark);
  border-radius: 0.5rem;
  font-size: 1rem;
  transition: all 0.2s;
  background-color: white;
}

.form-input:focus {
  outline: none;
  border-color: var(--color-primary);
  box-shadow: 0 0 0 3px rgba(0,138,204,0.1);
}

.form-input::placeholder {
  color: var(--color-text-light);
}

/* Error Message */
.error {
  background-color: #fff5f5;
  color: var(--color-error);
  padding: 1rem;
  border-radius: 0.5rem;
  margin: 1.5rem 0;
  text-align: center;
  font-size: 0.875rem;
  font-weight: 500;
  border: 1px solid rgba(229, 62, 62, 0.1);
}

/* Auth Buttons */
.auth-form .btn {
  width: 100%;
  margin-top: 1rem;
}

.auth-form .btn-primary {
  background: var(--gradient-primary);
  color: white;
  border: none;
}

.auth-form .btn-outline {
  background: transparent;
  border: 2px solid var(--color-primary);
  color: var(--color-primary);
  margin-top: 1rem;
}

.auth-form .btn-outline:hover {
  background: var(--gradient-primary);
  color: white;
  border-color: transparent;
}

/* Auth Links */
.auth-links {
  margin-top: 2rem;
  text-align: center;
}

.auth-links p {
  margin-bottom: 1rem;
  color: var(--color-text-light);
}

/* Terms Text */
.terms-text {
  margin-top: 2rem;
  text-align: center;
  font-size: 0.875rem;
  color: var(--color-text-light);
}
